html {
  --heading-size: 25px;
  --heading-base-width: 20rem;
  --font-icon-size: 72px;
  --font-greeting-size: 5.5rem;
  --font-name-size: 3rem;
  --font-status-size: 1.5rem;
  --img-size: 15rem;
  --font-hobby-size: 1.5rem;
}


.section-download-btn {
  /* position: absolute;
  top: 0;
  right: 0; */
  width: 12rem;
  padding: 1.5rem;
  font-size: .9rem;
  color: #FCFFF5;
  background-color: #222831;
  align-self: center;
  border: 2px solid;
}

.header-hobby {
  padding: .5rem;
  margin: .5rem;
  font-size: var(--font-hobby-size);
}

.cursive {
  font-family: 'Leckerli One', cursive;
}

.section-download-btn:hover {
  background-color: #FCFFF5;
  color: #222831;
}

a:hover {
  text-decoration: none;
  color: inherit;
}

.section-header {
  height: 80vh;
  width: 100%;
  background-color: #222831;
  color: #FCFFF5;
  display: grid;
  place-content: center;
  grid-template-rows: 60vh 1fr;
}

.header-content {
  display: flex;
  align-self: center;
  place-content: space-evenly;
}

.header-contact{
  color: #D1DBBD;
  /* position: absolute; */

  /* grid-column: 1 / -1; */
  grid-row: 3;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-items: center;
  align-content: center;
  /* height: 10rem; */
}

.remove-default {
  text-decoration: none;
  color: #D1DBBD;
}

.header-contact-icon {
  font-size: var(--font-icon-size);
}

.highlight {
  text-decoration: none;
  display: inline;
  background-color: #ecd018;
  padding: 6px;
  border-radius: 4px;
}

.highlight-word {
  color: #D1DBBD;
}

.header-photo {
  width: 100%;
  height: 100%;
  clip-path: circle(40% at 50% 50%);
  object-fit: cover;
}

.section-white-banner {
  background-color: #3E606F;
  border-bottom: 5px solid var(--highlight-border-color);
}


.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #4876EB;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.header-img {
  width: var(--img-size);
  height: var(--img-size);
}
.home-photo-img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  /* transform: translateY(-115px) scale(1.1); */
  border-radius: 16rem;
  border: 5px solid #D1DBBD;
}

.header-greating {
  font-size: var(--font-greeting-size);
  font-weight: 300;
  line-height: 1.2;

}

.header-name {
  font-size: var(--font-name-size);
}

.header-status {
  font-size: var(--font-status-size)
}

.border-right  {
  border-right: 2px solid;
}

@media only screen and (max-width: 600px) {
  html {
    --heading-size: 25px;
    --heading-base-width: 20rem;
    --font-icon-size: 48px;
    --font-greeting-size: 2.2rem;
    --font-name-size: 2rem;
    --font-status-size: 1.5rem;
    --font-hobby-size: 0.75rem;
    --img-size: 13rem;
  }
  /* .home-photo-img {
     transform: translateY(-90px) scale(1.5);
  } */
  .highlight {
    padding: .4rem;
  }
  .section-download-btn {
    width: 100%;
    padding: 2rem;
    border: none;
    grid-column: 1 / -1;
  }
  .mobile-hide {
    display: none;
  }
  .header-content {
    flex-direction: column;
    align-items: center;
  }
  .header {
    margin-top: 2rem;
  }

  .section-header {
    height: 75vh;
  }
  .header-contact {
    grid-template-columns: repeat(3,1fr)
  }
}


.header {
  display: flex;
  flex-direction: column;
  place-content: center;
}

#cursor {
  position: fixed;
  text-align: center;
  width: 100px;
  height: 100px;
  z-index: 1000;
  -webkit-transform-origin: center center;
  transform-origin: center center;
  pointer-events: none;
  mix-blend-mode: exclusion;
  opacity: 0.7;
}

.cursor__label {
  position: absolute;
  color: black;
  top: 50%;
  left: 50%;
  -webkit-transform: translate3d(-50%,-50%,0);
  transform: translate3d(-50%,-50%,0);
  z-index: 1;
  display: block;
  width: 100px;
  height: 100px;
  line-height: 100px;
  font-size: 1.1em;
  font-weight: 600;
  letter-spacing: .14384615em;
  text-transform: uppercase;
}

.cursor__bg {
  -webkit-transition: transform .3s ease-in-out;
  -webkit-transition: -webkit-transform .3s ease-in-out;
  transition: -webkit-transform .3s ease-in-out;
  transition: transform .3s ease-in-out;
  transition: transform .3s ease-in-out,-webkit-transform .3s ease-in-out;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  display: block;
  left: 0;
  top: 0;
  mix-blend-mode: exclusion;
  background: #fff;
  width: 100px;
  height: 100px;
  border-radius: 100px;
  -webkit-transform: scale(.15);
  transform: scale(.8);

}